<style type="text/css" lang='less'>
.growthFileReport {
	width: 100%;
	padding-top: 20px;
	padding-bottom: 20px;
	background-color: #E8E8E8;
	color: #333;
	font-size: 16px;
	.get_pdf {
		height: 0px;
		width: 100%;
		position: fixed;
		top: 30%;
		.inner {
			width: 900px;
			margin: 0 auto;
			position: relative;
			.btn_pdf {
				position: absolute;
				padding: 5px 10px;
				right: -40px;
				width: 20px;
				text-align: center;
				background-color: #2c8ffc;
				color: #fff;
				cursor: pointer;
				border-radius: 5px;
			}
		}
	}
	.pdf {
		width: 900px;
		margin: 0 auto;
		background-color: #fff;
		.page_shadow{
			-webkit-box-shadow: 0px 0px 10px #666;
			-ms-box-shadow: 0px 0px 10px #666;
			-moz-box-shadow: 0px 0px 10px #666;
			height: 1300px;
			width: 900px;
			margin-bottom: 20px;
		}
		.page {
			box-sizing: border-box;
			height: 1300px;
			width: 900px;
			padding: 50px;
			background: url(/imgs/growthFile/report_bg.png) no-repeat;
			background-size: 900px 1300px;
			background-color: #fff;
			position: relative;
			.report_header {
				margin-bottom: 20px;
				line-height: 50px;
				color: #2E75B6;
				font-size: 24px;
				padding-left: 100px;
				background: url('/imgs/growthFile/icon_smallbg.png') no-repeat 0px center;
				background-size: 80px 30px;
			}
			.big_page_number {
				position: absolute;
				bottom: 0px;
				left: 0px;
				width: 100%;
				line-height: 40px;
				font-size: 18px;
				color: #666;
				text-align: center;
			}
		}
	}
	.pdfs {
		position: relative;
		// top: -10000000px;
		// right: -10000px;
		.pdf;
	}
}
